<template>
<v-container>
    <v-row>
      
        <v-col cols="12" md="3" v-for="(list, i) in lists" :key="i">
            <v-card class="mx-auto" max-width="400">

                <v-card-title class="pb-0">{{list.name}} 
                    <v-btn @click="editList(list,i)" class="mx-2" fab dark small color="cyan">
                        <v-icon dark>mdi-pencil</v-icon>
                    </v-btn>
                    <v-btn @click="removeList(list,i)" class="mx-2" fab dark small color="red">
                        X
                    </v-btn>
                    <span class="success--text" style="font-size: 14px;font-weight: 700;">
                        ( {{
                           parseInt(((list.items.filter(x => x.done == true).length) /  (list.items.length)) *100)
                        }}% )
                        </span>
                </v-card-title>
                <v-dialog v-model="lisEdit" max-width="290">
                    <v-card class="kk_editModal">
                        <v-card-title class="headline">Edit List</v-card-title>
                        <v-card-text>
                            <input class="edit" type="text" v-model="editListValue" @keyup.enter="doneEdit(editListId)" style="margin-top: 10px;
                          width: 100%;" />
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>

                            <v-btn color="error" small @click="lisEdit = false">
                                Abort
                            </v-btn>

                            <v-btn color="primary" small @click="doneEdit(editListId)">
                                Save
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

                <v-card-text class="text--primary">
                    <v-list>
                        <v-list-item-group color="primary">

                            <v-list-item v-for="(item, ii) in list.items" :key="ii">
                                <v-list-item-icon>
                                    <v-checkbox v-model="item.done"></v-checkbox>
                                </v-list-item-icon>
                                <v-list-item-content>
                                    <v-list-item-title @dblclick="editTodo(item,ii)" v-text="item.name" :class="{ completed : item.done }" :id="'item'+ii"></v-list-item-title>
                                    <input style="display:none" class="edit" :id="'editInput'+ii" type="text" v-model="item.name" @keyup.enter="doneEditTodo(item, ii)" @keyup.esc="cancelEditTodo(item, ii)" />
                                </v-list-item-content>
                                <v-btn @click="removeTodo(item,i)" class="mx-2" fab dark small color="red">
                                    X
                                </v-btn>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>
                </v-card-text>

                <v-card-actions>
                    <input class="new-todo" autofocus autocomplete="off" placeholder="What needs to be done?" v-model="newTodo[i]" @keyup.enter="addTodo(i)" />
                    <v-btn color="primary" class="white--text" @click="addTodo(i)">
                        <v-icon right dark>mdi-chevron-right</v-icon>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-col>
    </v-row>
</v-container>
</template>

<script>
export default {
    name: "Todo",
    props: ['user_id','lists'],
    components: {},
    data: () => ({
        newTodo: {},
        lisEdit: false,
        editListValue: '',
        editListId: '',
        editedTodo: '',
        percentage: [],
        listShow: true,
        editShow: false,
        newLists : {},
    }),
    methods: {
        addTodo: function (i) {

            var value = this.newTodo[i];
            if (!value) {
                return;
            }
            this.lists[i].items.push({
                name: value,
                done: false
            });
            this.newTodo = {};
        },

        editList: function (list, i) {

            this.editListValue = list.name;
            this.editListId = i;
            this.lisEdit = true;
        },

        doneEdit: function (i) {
            if (!this.editListValue) {
                return;
            }
            this.lists[i].name = this.editListValue;
            this.lisEdit = false;
            this.editListValue = null;
        },
        removeTodo: function (item, i) {
            this.lists[i].items.splice(this.lists[i].items.indexOf(item), 1);
        },
   
        removeList: function (list) {
             if(confirm("Do you really want to delete?")){
            this.lists.splice(this.lists.indexOf(list), 1);
           }
            
        },

        editTodo: function (item, ii) {
            var listItem = document.getElementById("item" + ii);
            var editInput = document.getElementById("editInput" + ii);
            listItem.style.display = "none";
            editInput.style.display = "block";

            this.beforeEditCache = item.name;
            this.editedTodo = item;
        },

        doneEditTodo: function (item, ii) {
            if (!this.editedTodo) {
                return;
            }
            this.editedTodo = null;
            item.name = item.name.trim();
            if (!item.name) {
                this.removeTodo(item);
            }
            var listItem = document.getElementById("item" + ii);
            var editInput = document.getElementById("editInput" + ii);
            listItem.style.display = "block";
            editInput.style.display = "none";
        },

        cancelEditTodo: function (item, ii) {
            this.editedTodo = null;
            item.name = this.beforeEditCache;

            var listItem = document.getElementById("item" + ii);
            var editInput = document.getElementById("editInput" + ii);
            listItem.style.display = "block";
            editInput.style.display = "none";
        },
    },
    // created(){
    //     // this.loadData(this.user_id)
   
    // },
    // watch: {
    //   // whenever question changes, this function will run
    //   newLists: function (newQuestion) {
       
    //   }
    // },
}
</script>

<style>
.completed {
    text-decoration: line-through;
}

.v-card__title {
    border-bottom: 1px solid #dedede;
    padding: 10px;
}

button.mx-2.v-btn.v-btn--contained.v-btn--fab {
    border-radius: 5px;
    box-shadow: unset;
}

.v-btn--fab.v-size--small {
    height: 20px !important;
    width: 20px !important;
}

.v-btn--icon.v-size--small .v-icon,
.v-btn--fab.v-size--small .v-icon {
    height: 24px;
    font-size: 14px !important;
    width: 24px;
}

.v-list-item {
    padding: 0px !important
}

.v-list-item__icon {
    margin: 0px !important;
}

.v-card__actions {
    border-top: 1px solid #dedede;
}

input.new-todo {
    width: 100%;
    border: 1px solid #cecdcd;
    padding: 5px;
    border-radius: 0px;
}

.v-card__actions>.v-btn.v-btn {
    border-radius: 0;
}

input.edit {
    border: 1px solid #d6d6d6;
    padding: 7px;
    border-radius: 5px;
}

button.v-btn.v-btn--contained.theme--light.v-size--small {
    width: 50%;
    margin: 0px !important;
    box-shadow: unset;
}
</style>
