<template>
<v-app>
    <!-- mobile sidebar start -->
    <v-app-bar dense fixed style="var(--v-box_shadow-base)" class="d-md-none" color="dark" dark>
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

    </v-app-bar>

    <v-navigation-drawer v-model="drawer" fixed temporary dark>
        <v-list dense>
            <v-list-item-group>
                <v-list style="background: none;">
                    <v-list-item-group color="primary">

                <v-list-item v-for="(user, i) in users" :key="i" class="">
                    <v-btn color="primary" class="ma-2 white--text" @click="getList(user.id)">
                        <v-icon left dark>mdi-account</v-icon>
                        {{user.name}}
                    </v-btn>
                </v-list-item>
                <v-list-item class="">
                    <v-btn color="primary" class="ma-2 white--text" dark @click.stop="user_dialog = true">
                        <v-icon>mdi-plus</v-icon>
                    </v-btn>
                </v-list-item>


                    </v-list-item-group>
                </v-list>

            </v-list-item-group>
        </v-list>
    </v-navigation-drawer>

    <!-- desktop navbar start  -->
    <v-app-bar app class="d-md-block" dense color="dark" dark>
        <v-list style="background: none;">
            <v-list-item-group color="primary">

                <v-list-item v-for="(user, i) in users" :key="i" class="float-left">
                    <v-btn color="primary" class="ma-2 white--text" @click="getList(user.id)">
                        <v-icon left dark>mdi-account</v-icon>
                        {{user.name}}
                    </v-btn>
                </v-list-item>
                <v-list-item class="">
                    <v-btn color="primary" class="ma-2 white--text" dark @click.stop="user_dialog = true">
                        <v-icon>mdi-plus</v-icon>
                    </v-btn>
                </v-list-item>

            </v-list-item-group>
        </v-list>

    </v-app-bar>
    <!-- desktop navbar end  -->

    <v-content>
        <Todo :user_id="user_id" :lists="lists" />
    </v-content>
    <!-- modal start  -->
    <v-row>
        <v-btn color="primary" class="modal-btn" dark @click.stop="dialog = true">
            <v-icon>mdi-plus</v-icon>
        </v-btn>

        <v-dialog v-model="dialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Add New List</v-card-title>

                <v-card-text>
                    <input class="new-todo" autofocus autocomplete="off" placeholder="What needs to be done?" v-model="newList" @keyup.enter="addList()" style="margin-top: 10px;
                          width: 100%;border-radius:5px" />
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn color="error" small @click="dialog = false">
                        Abort
                    </v-btn>

                    <v-btn color="primary" small @click="addList()">
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>

    <!-- //add user modal  -->
    <v-row>
        <v-dialog v-model="user_dialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Add New User</v-card-title>

                <v-card-text>
                    <input class="new-todo" autofocus autocomplete="off" placeholder="Enter Full Name" v-model="newUser" @keyup.enter="addUser()" style="margin-top: 10px;
                          width: 100%;border-radius:5px" />
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn color="error" small @click="user_dialog = false">
                        Abort
                    </v-btn>

                    <v-btn color="primary" small @click="addUser()">
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</v-app>
</template>

<script>
import Todo from './components/Todo';

export default {
    name: 'App',

    components: {
        Todo,
    },

    data: () => ({
        drawer: null,
        dialog: false,
        user_dialog: false,
        newList: '',
        newUser: '',
        todos: [],
        user: 1,
        user_id: 1,
        users: [{
                id: 1,
                name: 'Test User'
            },
            {
                id: 2,
                name: 'Kowsar'
            },
        ],

        allData: [{
                user: 1,
                lists: [{
                        name: 'Test User Todo',
                        items: [{
                                name: 'Test Item',
                                done: false
                            },
                            {
                                name: 'Test complete',
                                done: true
                            },
                            {
                                name: 'Test complete 2',
                                done: true
                            },
                        ],
                    },
                    {
                        name: 'Test User Todo 2',
                        items: [{
                                name: 'Test Item',
                                done: true
                            },
                            {
                                name: 'Test complete',
                                done: true
                            },
                        ],
                    },
                ],
            },
            {
                user: 2,
                lists: [{
                        name: 'Kowsar Todo',
                        items: [{
                                name: 'Test Item',
                                done: false
                            },
                            {
                                name: 'Test complete',
                                done: true
                            },
                        ],
                    },
                    {
                        name: 'Kowsar Todo 2',
                        items: [{
                                name: 'Test Item',
                                done: false
                            },
                            {
                                name: 'Test complete',
                                done: true
                            },
                        ],
                    },
                ],
            },
        ]
    }),
    methods: {
        addUser: function () {

            var value = this.newUser;

            if (!value) {
                return;
            }
            var last_id = this.users[this.users.length - 1];
            var new_id = last_id.id + 1
            this.users.push({
                name: value,
                id: new_id,
            });

            this.allData.push({
                user: new_id,
                lists: [{
                    name: 'Test User Todo',
                    items: [],
                }],
            });
            this.newUser = '';
            this.user_dialog = false;
        },
        addList: function () {

            var value = this.newList;

            if (!value) {
                return;
            }
            // console.log(this.lists)
            this.lists.push({
                name: value,
                items: [],
            });
            this.newList = '';
            this.dialog = false;
        },

        loadData: function (user_id) {

            let arr = [];

            this.allData.forEach((value) => {
                if (value.user == user_id) {
                   
                    arr.push(value.lists);
                }
            });

            return arr[0]

        },

        getList: function (user_id) {
            const res = this.loadData(user_id)

            this.lists = res
            this.user_id = user_id
            this.$emit('update:lists', this.lists)
        }
    },
    created() {
        const res = this.loadData(this.user)
        this.lists = res
    },
};
</script>

<style scoped>
.v-list-item.v-list-item--link {
    float: left;
}

.modal-btn {
    float: right;
    position: absolute;
    right: 0;
    top: 50%;
}

@media screen {
    .d-md-block {
        display: none;
    }
}
</style>
